body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #CAD2C5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  position: relative;
  min-height: 100%;
}

.navbar {
  background: #2f3e46de;
}

.navbar-brand {
  font-size: 40px;
  color: white;
  font-family: "Comfortaa"; 
}

.nav-link {
  color: white;
  font-family: "Comfortaa";
  margin-right: 25px;
  font-size: 25px;
}

.nav-link:hover {
  color:black
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;  
}       

.footer-text {
  color: black;
  font-family: "Comfortaa";
}

.name {
  font-family: "Comfortaa";
  font-weight: bold;
}

.email {
  font-family: "Comfortaa";
  font-weight: bold;
}

.message {
  font-family: "Comfortaa";
  font-weight: bold;
}

.submit {
  font-family: "Comfortaa";
  border-radius: 6px;
  background: teal;
  font-weight: bold;
}

.submit:hover {
  background-color:rgb(0, 92, 92);
}

.title {
  font-family: "Comfortaa";
  font-weight: bold;
}

.about {
  font-family: "Comfortaa";
  font-size: 17px;
  font-weight: bold;
} 

.about-page {
  width: 700px;
  margin: 0 auto;
}

.page-container {
  position: relative;
  min-height: 100vh;
}

.content-wrap {
  padding-bottom: 2.5rem;    /* Footer height */
}

.github {
  color: black;
}

.github:hover {
  color: teal
}

.linkedin {
  color: black;
}

.linkedin:hover {
  color: teal
}